  setArraySize(){
    if(this.state.size !== '' && this.state.size >= 8){
      if(this.state.size > MAX_ARRAY_SIZE){
        array_size = MAX_ARRAY_SIZE;
      }
      else {
        array_size = this.state.size;
      }
    }
  }

  setSpeed(){
    if(this.state.speed !== '' && this.state.speed >= 5){
      if(this.state.speed < 5){
        speed = MIN_SPEED;
      }
      else {
        speed = this.state.speed;
      }
    }
  }



