import bar_outline from './icons/bar-chart-outline.svg';

const PRIMARY_COLOR = '#3244a8';
const SECONDARY_COLOR = '#d40f0f';
const WIN_WIDTH = (window.innerWidth) /1.6;
const WIN_HEIGHT = (window.innerHeight) / 1.7;
const MIN_ARRAY_SIZE = 8;
const MAX_ARRAY_SIZE = Math.floor(WIN_WIDTH/5);
var BAR_WIDTH = Math.floor(WIN_WIDTH / MIN_ARRAY_SIZE);
var TEXT_SIZE = (BAR_WIDTH)/2;
const MIN_SPEED = 5;
var array_size = 8;
var speed = 10;
/* Sorting Algorithm Fucntions*/

function bubbleSort(array){

  const animations = [];

  for(let i=0;i<array.length-1;i++){
    for(let j=0;j<array.length-i-1;j++){

      animations.push([j,j+1]);

      if(array[j] > array[j+1]){
        const temp = array[j];
        array[j] = array[j+1];
        array[j+1] = temp;
        animations.push([array[j] , array[j+1]]);
      }else{
        animations.push([array[j] , array[j+1]]);
      }

      animations.push([j,j+1]);
    }
  }
  return animations;
}

  //Function to animate the bar representing an array

export function animateBubbleSort(array, speed){

  const animations = bubbleSort(array);
  const arrayBars = document.getElementsByClassName('array-bar');
  for(let i=0;i<animations.length;i++){
    const isChange = i%3 !== 1;
    if(isChange){
      const [barOneIndex, barTwoIndex] = animations[i];
      const barOneStyle = arrayBars[barOneIndex].style;
      const barTwoStyle = arrayBars[barTwoIndex].style;
      const color = i%3 === 0 ? SECONDARY_COLOR : PRIMARY_COLOR;
      setTimeout(() => {
        barOneStyle.backgroundColor = color;
        barTwoStyle.backgroundColor = color;
      }, i * speed);
    }else{
      const [barOneIndex, barTwoIndex] = animations[i-1];
      const barOneStyle = arrayBars[barOneIndex].style;
      const barTwoStyle = arrayBars[barTwoIndex].style;
      const [barOneHeight, barTwoHeight] = animations[i];
      setTimeout(() => {
        const text = document.getElementsByClassName('bar-value');
        text[barOneIndex].innerHTML = barOneHeight;
        text[barTwoIndex].innerHTML = barTwoHeight
        barOneStyle.height = `${barOneHeight}px`;
        barTwoStyle.height = `${barTwoHeight}px`;
      },i*speed);
    }
  }
}

/*Drawer function for displaying code*/

function openCity(event, cityName){
  // Declare all variables
  var i, tabcontent, tablinks;
  // Get all elements with class="tabcontent" and hide them
  tabcontent = document.getElementsByClassName("tabcontent");
  for (i = 0; i < tabcontent.length; i++) {
    tabcontent[i].style.display = "none";
  }

  // Get all elements with class="tablinks" and remove the class "active"
  tablinks = document.getElementsByClassName("tablinks");
  for (i = 0; i < tablinks.length; i++) {
    tablinks[i].className = tablinks[i].className.replace(" active", "");
  }

  // Show the current tab, and add an "active" class to the button that opened the tab
  document.getElementById(cityName).style.display = "block";
  // evt.currentTarget.className += " active";
  let tag = document.getElementById(cityName);
  event.classname += " active";
  tag.click();
}


