  createArray(){
    var text;
    const array = this.state.input.split(" ");
    array_size = array.length;
    document.getElementById('user-array').value = '';
    const arrayBars = document.getElementsByClassName('array-bar');
    for(let k=0;k<this.state.array.length;k++){
      arrayBars[k].style.backgroundColor = PRIMARY_COLOR;
    }
    var setText = document.getElementById('info-text');
    if(array_size === MAX_ARRAY_SIZE){
      text = "Array of size " + array_size + ", i.e. maximum number of arrays, is created. <br>Note: To see step by step sorting, time delay should be greater than 500 milliseconds.";
    }else{
      text = "Array of size " + array_size + " is created. <br>Note: To see step by step sorting, time delay should be greater than 500 milliseconds.";
    }
    setText.innerHTML = text;
    BAR_WIDTH = WIN_WIDTH / array_size;
    TEXT_SIZE = BAR_WIDTH/2;
    for(let p=0;p<array_size;p++)
    {
      array[p] = parseInt(array[p]);
    }
    this.setState({array});
  }

