import React from 'react';
import './App.css';

import Home from './Content/Home.js';
import Bubble from './Content/Bubble.js'
import Insertion from './Content/Insertion.js'
import Selection from './Content/Selection.js'
import Merge from './Content/InPlaceMerge.js'
import Quick from './Content/Quick.js'
import Heap from './Content/Heap.js'

import {Route} from 'react-router-dom'

function App() {
  return (
    <div className="App">
      <Route exact path="/" component={Home} />
      <Route path="/bubble-sort" component={Bubble} />
      <Route path="/insertion-sort" component={Insertion} />
      <Route exact path="/selection-sort" component={Selection} />
      <Route path="/inplace-merge-sort" component={Merge} />
      <Route path="/quick-sort" component={Quick} />
      <Route path="/heap-sort" component={Heap} />
    </div>
  );
}

export default App;
