import React, {Component} from 'react';
import './CSS/Home.css'
import 'bootstrap/dist/css/bootstrap.min.css';

import algo from './images/algo.jpg'
import bar_outline from './icons/bar-chart-outline.svg'
import infinite_outline from './icons/infinite-outline.svg'
import hourglass from './icons/hourglass-outline.svg'
import reader from './icons/reader-outline.svg'
import { HashLink as Link } from 'react-router-hash-link';
import dp1 from './images/algoviz-dp-1.jpg';
import dp2 from './images/algoviz-dp-2.jpg';
import dp3 from './images/algoviz-dp-3.jpg';
import dp4 from './images/algoviz-dp-4.jpg';

class Home extends Component {

  render() {
    return (
      <div className="medium-12 columns">
      <nav>
      <Link to="/">
      <div class="logo">
      <img src={bar_outline} alt={"Logo"}/>
      <h4>AlgoViz</h4>
      </div>
      </Link>
      <ul class="nav-links">
      <li >
      <Link to="#"  class="btn-text nav-a">Home</Link>
      </li>
      <li>
      <Link to="#section-about" class="btn-text nav-a">About</Link>
      </li><li>
      <Link to="#section-tours" class="btn-text nav-a">Algorithms</Link>
      </li>
      </ul>
      </nav>

      <main>

      <header class="header" id="header">
      <div class="header__text-box">
      <h1 class="heading-primary">
      <span class="heading-primary--main">AlgoViz</span>
      <span class="heading-primary--sub">The best way to understand data structures is to see them in action</span>
      </h1>

      <Link to="#section-tours" class="btn btn--white btn--animated dis">Learn Algorithms</Link>
      </div>
      </header>


      <section class="section-about" id="section-about">
      <div class="u-center-text u-margin-bottom-big ">
      <h2 class="heading-secondary">
      Understand complex data structures
      </h2>
      </div>

      <div class="row">
      <div class="col-1-of-2">
      <h3 class="heading-tertiary u-margin-bottom-small">The best way to understand data structures is to see them in action</h3>
      <p class="paragraph">
      Acquiring knowledge about algorithms is always a difficult and complex process. Every time we had to visulaize
      the pattern in which the algorithm is performing and for complex data structures involving many transitions in a single step. Hence, this will
      make your task easy.
      </p>

      <h3 class="heading-tertiary u-margin-bottom-small">Easy learning</h3>
      <p class="paragraph">
      It can be made easy after plotting graphically to understand in a better and more convinient way.
      </p>


      </div>
      <div class="col-1-of-2">
      <div class="composition">

      <img
      sizes="(max-width: 56.25em) 20vw, (max-width: 37.5em) 30vw, 300px"
      alt="Icon 1"
      class="composition__photo composition__photo--p1"
      src={require('./images/dijikstra.jpg')} />

      <img
      sizes="(max-width: 56.25em) 20vw, (max-width: 37.5em) 30vw, 300px"
      alt="Icon 3"
      class="composition__photo composition__photo--p3"
      src={require('./images/bar-small.jpg')} />

      <img
      sizes="(max-width: 56.25em) 20vw, (max-width: 37.5em) 30vw, 300px"
      alt="Icon 2"
      class="composition__photo composition__photo--p2"
      src={algo} />




      </div>
      </div>
      </div>
      </section>

      <section class="section-features">

      <div class="row">
      <div class="col-1-of-4">
      <div class="feature-box">
      <i class="feature-box__icon "></i>
      <img src={bar_outline} class="icn"  alt={"Bar-chart"}/>
      <h3 class="heading-tertiary u-margin-bottom-small" >visualization of algorithms</h3>
      <p class="feature-box__text">
      Visual illustration of an algorithms operation, of its performance on different kinds of inputs and better understanding.
      </p>
      </div>
      </div>

      <div class="col-1-of-4">
      <div class="feature-box">
      <i class="feature-box__icon "></i>
      <img src={infinite_outline} class="icn"  alt={"Infinity-symbol"}/>
      <h3 class="heading-tertiary u-margin-bottom-small">Deeper knowledge</h3>
      <p class="feature-box__text">
      How complexity depends on input, the iterations and choosing the best one out of many.
      </p>
      </div>
      </div>

      <div class="col-1-of-4">
      <div class="feature-box">
      <i class="feature-box__icon "></i>
      <img src={hourglass} class="icn" alt={"Hourglass"}/>
      <h3 class="heading-tertiary u-margin-bottom-small">Learn at your own pace</h3>
      <p class="feature-box__text">
      Looking onto each step and comparing the code with the side by side execution. Delay factor to help you to learn accordingly.
      </p>
      </div>
      </div>

      <div class="col-1-of-4">
      <div class="feature-box">
      <i class="feature-box__icon "></i>
      <img src={reader} class="icn" alt={"reader"}/>
      <h3 class="heading-tertiary u-margin-bottom-small">Last minute guide</h3>
      <p class="feature-box__text">
      In a hurry? You are all sorted for the simplest and the important concepts.
      </p>
      </div>
      </div>
      </div>
      </section>


      <section class="section-tours" id="section-tours">
      <div class="u-center-text u-margin-bottom-big">
      <h2 class="heading-secondary">
      Sorting Algorithms
      </h2>
      </div>

      <div class="row">
      <div class="col-1-of-3">
      <div class="card">
      <div class="card__side card__side--front">
      <div class="card__picture card__picture--1">
      &nbsp;
      </div>
      <h4 class="card__heading">
      <span class="card__heading-span card__heading-span--1">Bubble Sort</span>
      </h4>
      <div class="card__details">
      <ul>


      <li>Time complexity : O(n<sup>2</sup>)</li>
      <li>Space complexity : O(1)</li>
      <li>Difficulty: easy - medium</li>
      <li> <Link to="/bubble-sort#"  class="btn-explore">Explore</Link> </li>
      </ul>
      </div>
      </div>
      </div>
      </div>


      <div class="col-1-of-3">
      <div class="card">
      <div class="card__side card__side--front">
      <div class="card__picture card__picture--2">
      &nbsp;
      </div>
      <h4 class="card__heading">
      <span class="card__heading-span card__heading-span--2">Insertion Sort</span>
      </h4>
      <div class="card__details">
      <ul>

      <li>Time complexity : O(n<sup>2</sup>)</li>
      <li>Space complexity : O(1)</li>
      <li>Difficulty: hard </li>
      <li> <Link to="/insertion-sort#"  class="btn-explore">Explore</Link> </li>
      </ul>
      </div>

      </div>
      </div>
      </div>


      <div class="col-1-of-3">
      <div class="card">
      <div class="card__side card__side--front">
      <div class="card__picture card__picture--3">
      &nbsp;
      </div>
      <h4 class="card__heading">
      <span class="card__heading-span card__heading-span--3">Selection Sort</span>
      </h4>
      <div class="card__details">
      <ul>
      <li>Time complexity : O(n<sup>2</sup>)</li>
      <li>Space complexity : O(1)</li>
      <li>Difficulty: easy - medium</li>
      <li> <Link to="/selection-sort#"  class="btn-explore">Explore</Link> </li>
      </ul>
      </div>

      </div>
      </div>
      </div>
      </div>

      <div style={{padding: '3rem',}}>

      </div>

      <div class="row">
      <div class="col-1-of-3">
      <div class="card">
      <div class="card__side card__side--front">
      <div class="card__picture card__picture--1 crdpic4">
      &nbsp;
      </div>
      <h4 class="card__heading">
      <span class="card__heading-span card__heading-span--1"> In-Place Merge Sort</span>
      </h4>
      <div class="card__details">
      <ul>
      <li>Time complexity : O(nlogn)</li>
      <li>Space complexity : O(n)</li>
      <li>Difficulty: easy - medium</li>
      <li> <Link to="/inplace-merge-sort#"  class="btn-explore">Explore</Link> </li>
      </ul>
      </div>
      </div>
      </div>
      </div>


      <div class="col-1-of-3">
      <div class="card">
      <div class="card__side card__side--front">
      <div class="card__picture card__picture--2 crdpic5">
      &nbsp;
      </div>
      <h4 class="card__heading">
      <span class="card__heading-span card__heading-span--2">Quick Sort</span>
      </h4>
      <div class="card__details">
      <ul>
      <li>Time complexity : O(nlogn)</li>
      <li>Space complexity : O(logn)</li>
      <li>Difficulty: hard </li>
      <li> <Link to="/quick-sort#"  class="btn-explore">Explore</Link> </li>
      </ul>
      </div>

      </div>
      </div>
      </div>


      <div class="col-1-of-3">
      <div class="card">
      <div class="card__side card__side--front">
      <div class="card__picture card__picture--3 crdpic6">
      &nbsp;
      </div>
      <h4 class="card__heading">
      <span class="card__heading-span card__heading-span--3">Heap Sort</span>
      </h4>
      <div class="card__details">
      <ul>
      <li>Time complexity : O(nlogn)</li>
      <li>Space complexity : O(1)</li>
      <li>Difficulty: easy - medium</li>
      <li> <Link to="/heap-sort#"  class="btn-explore">Explore</Link> </li>
      </ul>
      </div>

      </div>
      </div>
      </div>
      </div>

      </section>

      <footer class="footer" id="footer">
      <div class="u-center-text">
      <h2 class="heading-secondary">
      ALGOVIZ TEAM
      </h2>
      </div>

      <div class="row">
        <div class="col-1-of-2">
          <div class="footer-card" styel={{backgroundColor: "#ffffff" }}>
            <div class="col-1-of-2">
              <img src={dp1} />
            </div>
            <div class="col-1-of-2">
              <h3>Piyush Jain</h3>
              <h3>B.Tech CSE</h3>
              <h3>03315602717</h3>
            </div>
          </div>
        </div>
        <div class="col-1-of-2">
          <div class="footer-card">
            <div class="col-1-of-2">
              <img src={dp2} />
            </div>
            <div class="col-1-of-2">
              <h3>Archana Walecha</h3>
              <h3>B.Tech CSE</h3>
              <h3>00715602717</h3>
            </div>
          </div>
        </div>
      </div>
      <div class="row">
      <div class="col-1-of-2">
        <div class="footer-card">
          <div class="col-1-of-2">
            <img src={dp3} />
          </div>
          <div class="col-1-of-2">
            <h3>Shruti Rustagi</h3>
            <h3>B.Tech CSE</h3>
            <h3>04715602717</h3>
          </div>
        </div>
      </div>
      <div class="col-1-of-2">
        <div class="footer-card">
          <div class="col-1-of-2">
            <img src={dp4} />
          </div>
          <div class="col-1-of-2">
            <h3>Rohan Singh</h3>
            <h3>B.Tech CSE</h3>
            <h3>04215602717</h3>
          </div>
        </div>
      </div>
      </div>
      </footer>

      </main>

      </div>

    );
  }
}


export default Home;
